.game-container {
  display: grid;
  padding-top: 2rem;
}

.games,
.level-text-container {
  display: flex;
  justify-content: space-around;
}

.level-text-container {
  padding-bottom: 1.5rem;
}

.game-card {
  width: 15rem;
  height: 30rem;
  border: solid 3px black;
}

.game-card:hover {
  scale: 1.1;
}

.game1 {
  background-image: url("../assets/game1crop.jpg");
  background-size: cover;
  border: "solid 3px black";
}

.game2 {
  background-image: url("../assets/game2crop.png");
  background-size: cover;
}

.game3 {
  background-image: url("../assets/game3crop.jpg");
  background-size: cover;
}
